<template>
  <div id="show-blogs" v-theme:column="'wide'">
    <h1>博客总览</h1>
    <input type="text" placeholder="搜索" v-model="search">
    <div v-for="(blog,index) in filteredBlogs" :key="index" class="single-blog">
      <router-link v-bind:to="'/blog/' + blog.id">
        <h2 v-rainbow>{{blog.title | to-uppercase}}</h2>
      </router-link>
      <article>{{blog.content | snippet}}</article>
    </div>
  </div>
</template>

<script>

export default {
  name: 'show-blogs',
  data(){
    return {
      search:"",
      blogs:[],
    }
  },
  created(){
    // http://jsonplaceholder.typicode.com/posts
    this.$http.get("https://vuedemo-94b8e.firebaseio.com/posts.json").then((data)=>{
      // this.blogs=data.body.slice(0,10);
      return data.json();
    }).then(function(data){
      var blogsArray =[];
      for(let key in data){
        data[key].id=key;
        blogsArray.push(data[key]);
      }
      this.blogs = blogsArray;
    })
  },
  computed:{
    filteredBlogs(){
      return this.blogs.filter(blog=>{
        return blog.title.match(this.search);//boolean
      })
    }
  }
}
</script>

<style>
#show-blogs{
  max-width: 800px;
  margin: 0 auto;
}
.single-blog{
  padding: 20px;
  margin: 20px 0;
  box-sizing: border-box;
  background: #eee;
  border: 1px dotted #aaa;
}
#show-blogs a{
  color:#444;
  text-decoration: none;
}
input[type="text"]{
  padding: 8px;
  width:100%;
  box-sizing: border-box;
}

</style>